// pages/elenco.tsx

import React, { useState, useEffect } from 'react';
import Head from 'next/head';
import Layout from '../components/Layout';
import ModalNovoAluno from '../components/ModalNovoAluno'; // Importa o Modal
import { supabase } from '../lib/supabaseClient';
import { useRouter } from 'next/router';
import { Loader2, Plus, Edit, Trash2 } from 'lucide-react';
import Link from 'next/link';

// Tipagem para os dados de Aluno (garante consistência)
interface Aluno {
  id: number;
  nome_aluno: string;
  nome_pai: string;
  email_pai: string;
  data_matricula: string; // ou Date, se preferir objetos Date
  data_vencimento_mensalidade: string;
  status_mensalidade: 'paga' | 'proximo' | 'vencida';
  valor_mensalidade: number;
  // id_treinador e data_criacao_aluno não são necessários para exibição, mas estão no BD
}

const ElencoPage: React.FC = () => {
  const router = useRouter();
  const [alunos, setAlunos] = useState<Aluno[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showModal, setShowModal] = useState(false); // Estado para controlar a visibilidade do modal
  const [currentFilter, setCurrentFilter] = useState<'todos' | 'atrasados' | 'proximo' | 'paga'>('todos');

  const fetchAlunos = async () => {
    setLoading(true);
    setError(null);

    const { data: { user } } = await supabase.auth.getUser();
    if (!user) {
      router.push('/login');
      return;
    }

    let query = supabase
      .from('alunos')
      .select('*')
      .eq('id_treinador', user.id); // Filtra por treinador logado

    // Aplica o filtro de status
    if (currentFilter !== 'todos') {
      query = query.eq('status_mensalidade', currentFilter);
    }

    const { data, error: fetchError } = await query.order('nome_aluno', { ascending: true });

    if (fetchError) {
      console.error("Erro ao buscar alunos:", fetchError);
      setError("Não foi possível carregar o elenco. Tente novamente.");
      setLoading(false);
      return;
    }

    setAlunos(data || []);
    setLoading(false);
  };

  // Chama fetchAlunos na montagem do componente e quando o filtro muda
  useEffect(() => {
    fetchAlunos();
  }, [currentFilter]); 

  // Função para adicionar aluno no estado local após sucesso no modal
  const handleAlunoAdicionado = () => {
    setShowModal(false); // Fecha o modal
    fetchAlunos(); // Recarrega a lista de alunos para incluir o novo
  };

  // Formatação da data para exibição
  const formatDate = (dateString: string) => {
    const options: Intl.DateTimeFormatOptions = { year: 'numeric', month: '2-digit', day: '2-digit' };
    return new Date(dateString).toLocaleDateString('pt-BR', options);
  };

  // Formatação da moeda
  const formatCurrency = (value: number) => {
    return value.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' });
  };

  return (
    <>
      <Head>
        <title>Dribla | Elenco</title>
      </Head>
      {/* O Layout agora tem a responsabilidade de aplicar o background dark */}
      <Layout title="Gestão de Alunos">
        <div className="flex justify-between items-center mb-6">
          {/* Botão para abrir o modal */}
          <button 
            onClick={() => setShowModal(true)} 
            className="flex items-center px-4 py-2 bg-dribla-green text-gray-900 font-bold rounded-lg hover:bg-green-600 transition duration-200 shadow-md"
          >
            <Plus className="w-5 h-5 mr-2" /> Novo Atleta
          </button>
        </div>

        {/* Botões de Filtro */}
        <div className="flex space-x-2 mb-6">
          <button 
            onClick={() => setCurrentFilter('todos')} 
            className={`px-4 py-2 rounded-lg text-sm font-medium ${currentFilter === 'todos' ? 'bg-dribla-green text-gray-900' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
          >
            Todos ({alunos.length})
          </button>
          <button 
            onClick={() => setCurrentFilter('atrasados')} 
            className={`px-4 py-2 rounded-lg text-sm font-medium ${currentFilter === 'atrasados' ? 'bg-dribla-orange text-gray-900' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
          >
            Atrasados ({alunos.filter(a => a.status_mensalidade === 'vencida').length})
          </button>
          <button 
            onClick={() => setCurrentFilter('proximo')} 
            className={`px-4 py-2 rounded-lg text-sm font-medium ${currentFilter === 'proximo' ? 'bg-blue-500 text-white' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
          >
            Próximos a Vencer ({alunos.filter(a => a.status_mensalidade === 'proximo').length})
          </button>
          <button 
            onClick={() => setCurrentFilter('paga')} 
            className={`px-4 py-2 rounded-lg text-sm font-medium ${currentFilter === 'paga' ? 'bg-gray-500 text-white' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
          >
            Pagos ({alunos.filter(a => a.status_mensalidade === 'paga').length})
          </button>
        </div>

        {loading && (
          <div className="flex justify-center items-center py-10">
            <Loader2 className="w-10 h-10 text-dribla-green animate-spin" />
          </div>
        )}

        {error && (
          <p className="text-red-500 text-center mt-8">{error}</p>
        )}

        {!loading && alunos.length === 0 && (
          <div className="bg-gray-800 p-6 rounded-lg text-center text-gray-400 border border-gray-700">
            <p className="text-lg font-semibold mb-2">Nenhum atleta encontrado neste filtro.</p>
            <p>Tente um filtro diferente ou adicione um novo atleta.</p>
          </div>
        )}

        {!loading && alunos.length > 0 && (
          <div className="overflow-x-auto bg-gray-800 rounded-lg shadow-xl border border-gray-700">
            <table className="min-w-full divide-y divide-gray-700">
              <thead className="bg-gray-700">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Aluno
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Responsável
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Vencimento
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Valor
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Status
                  </th>
                  <th scope="col" className="relative px-6 py-3">
                    <span className="sr-only">Ações</span>
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-700">
                {alunos.map((aluno) => (
                  <tr key={aluno.id} className="hover:bg-gray-700 transition duration-150">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-white">{aluno.nome_aluno}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-300">{aluno.nome_pai}</div>
                      <div className="text-xs text-gray-500">{aluno.email_pai}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-300">{formatDate(aluno.data_vencimento_mensalidade)}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-300">{formatCurrency(aluno.valor_mensalidade)}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full 
                        ${aluno.status_mensalidade === 'paga' ? 'bg-green-100 text-green-800' : ''}
                        ${aluno.status_mensalidade === 'proximo' ? 'bg-blue-100 text-blue-800' : ''}
                        ${aluno.status_mensalidade === 'vencida' ? 'bg-red-100 text-red-800' : ''}
                      `}>
                        {aluno.status_mensalidade === 'paga' ? 'Pago' : ''}
                        {aluno.status_mensalidade === 'proximo' ? 'Próximo' : ''}
                        {aluno.status_mensalidade === 'vencida' ? 'Vencido' : ''}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <button className="text-dribla-green hover:text-green-600 mx-2">
                        <Edit className="w-5 h-5" />
                      </button>
                      <button className="text-red-500 hover:text-red-700 mx-2">
                        <Trash2 className="w-5 h-5" />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}

        {/* Modal de Novo Aluno (Condicionalmente renderizado) */}
        {showModal && (
          <ModalNovoAluno 
            onClose={() => setShowModal(false)} // Função para fechar o modal
            onAlunoAdicionado={handleAlunoAdicionado} // Função chamada após adicionar um aluno
          />
        )}
      </Layout>
    </>
  );
};

export default ElencoPage;

